import React, { ReactNode, useEffect, useState } from "react";
import "./styles/Grimoire.css";
import grimoireImage from '../images/Grimoire.png';
import candle from '../images/Candle.png';
import title from '../images/Title.png'
import PostSection from "./PostSection";
import WelcomePost from "./WelcomePost";
import Artifacts from "./Artifacts";
import Wizard from "./Wizard";

type GrimoireProps = {
  children: ReactNode;
};

const Grimoire: React.FC<GrimoireProps> = ({ children }) => {
  const [bookOpened, setBookOpened] = useState(false);
  const [activeSection, setActiveSection] = useState<string | null>(null);
  const [activeCategory, setActiveCategory] = useState<string | null>(null);
  const [menuOpen, setMenuOpen] = useState<boolean>(false);

  return (
    <>
      {!bookOpened && (
        <div className="MainContainer">
          <div className="Title"></div>
          <div className="Table">
            <div className="Altart">
              <div className="Candle"></div>
              <img src={grimoireImage} alt="Grimoire" className={`book-img ${bookOpened ? 'book-img-hidden' : ''}`} onClick={() => { setBookOpened(true) }} />
              <div className="Candle"></div>
            </div>
          </div>
        </div>
      )}
      {bookOpened && (
        <div className="modal-overlay"
          onClick={(e) => {
            if ((e.target as HTMLElement).classList.contains('modal-overlay')) {
              setBookOpened(false);
            }
          }}>
          <div className="modal-content">
            <aside className={`modal-menu ${menuOpen ? 'open' : 'collapsed'}`}>
              <ul>
                <li>
                  <button onClick={() => {setActiveSection('Artifacts');setActiveCategory(null);}}>Artifacts</button>
                </li>
                <li>
                  <button onClick={() => { setActiveSection('Development'); setActiveCategory(null); }}>Spells</button>
                  {activeSection === 'Development' && (
                    <aside>
                      <ul>
                        <li><button onClick={() => setActiveCategory('BackEnd')}>BackEnd</button></li>
                        <li><button onClick={() => setActiveCategory('FrontEnd')}>FrontEnd</button></li>
                        <li><button onClick={() => setActiveCategory('CI/CD')}> CI/CD</button></li>
                      </ul>
                    </aside>
                  )}
                </li>
                <li><button onClick={() => {setActiveSection('About Wizard');setActiveCategory(null);}}>About Wizard</button></li>
              </ul>
            </aside>
            <button
              className="menu-toggle"
              onClick={() => setMenuOpen(prev => !prev)}
            >
              {menuOpen ? '<<' : '>>'}
            </button>
            {activeSection === null && (
              <WelcomePost />
            )}
            {activeSection === 'Development' && (
              <PostSection category={activeCategory} />
            )}
            {activeSection === 'Artifacts' && (
              <Artifacts />
            )}
            {activeSection === 'About Wizard' && (
              <Wizard />
            )}
          </div>
        </div>
      )}
    </>
  );
};

export default Grimoire;